.navbar{
	background-color: black;
	overflow: hidden;
}

.navbar a{
	float: left;
	text-align: center;
	padding: 14px 16px;
	font-size: 17px;
	text-decoration: none;
	color: white;
}

.navbar a.active{
    background-color: #B0B7BC;
}

.navbar a:hover{
    background-color: #0076B6;
}